@import '../../../00_common/scss/variables';

.main {
  display: grid;
  //grid-template-columns: 375px 1fr;
  grid-template-columns: 7fr 1fr;

  .schedulerSettings {
    background: gold;
    display: grid;
    height: $toolbarHeight;
    padding: 5px;

    align-items: center;

    .timeDays {
      display: grid;
      grid-template-columns: 105px 1fr 1fr;
    }

    .daysOfWeek {
      display: grid;
      grid-template-columns: 105px auto;

      .checkBoxes {
        display: flex;
        font-size: 14px;
      }
    }

    .channel {
      display: grid;
      grid-template-columns: 105px auto;
    }

  }

  .viewSettings {
    background: plum;
    justify-content: center;
    align-content: center;

    display: grid;
    height: $toolbarHeight;
  }

}