@import '../00_common/scss/variables';

.app {
  display: grid;
  //grid-template-rows: auto 25px;
  grid-template-rows: auto $statusbarHeight;
  height: 100vh;

  .main {
    display: grid;
    grid-template-columns: $sidebarWidth auto;

    .sidebar {
      //border: 1px solid red;
      background: lightsteelblue;
    }

    .content {
      //border: 1px solid blue;
      display: grid;
      //grid-template-rows: 75px 50px auto;
      grid-template-rows: $topHeight $tabsHeight auto;

      .top {
        //border: 1px solid green;
        background: lightcoral;
      }

      .tabs {
        //border: 1px solid coral;
        background: lightcyan;
      }

      .body {
        //border: 1px solid darkmagenta;
        //height: calc(100% - 75px - 75px);
        background: lightyellow;
      }
    }
  }

  .statusbar {
    background: darkcyan;
  }
}