@import '../../00_common/scss/variables';

.main {
  display: grid;
  grid-template-columns: $timelineWidth calc(100vw - (#{$sidebarWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize})) $timelineWidth $scrollSize;


  .left {
    background: blue;
  }

  .scrollLine {
    //background: fuchsia;
    width: calc(100vw - (#{$sidebarWidth} + #{$timelineWidth} + #{$timelineWidth} + #{$scrollSize}));
    overflow-x: scroll;

    .content {
      //background: springgreen;
      //background: blue;
      //width: 1500px;
      height: 1px;
      //overflow-x: scroll;
    }
  }

  .right {
    background: blue;
  }

  .gap {
    background: limegreen;
  }
}


